.layout {
    &-sticky {
        &-container {
            display: flex;

            .layout-sidebar {
                position: sticky;
                top: 115px;
                height: calc(100vh - 115px);
                z-index: 1000;
                width: 356px;
                background-color: $color-white;
                border-right: 1px solid $color-gray-30;
                box-shadow: 0px 0px 0px 1px rgba(255, 255, 255, 0.00),
                    0px 6px 12px -3px rgba(156, 165, 186, 0.04),
                    0px 6px 18px 0px rgba(156, 165, 186, 0.12);
                transition: transform 0.3s ease;

                @media (max-width: 1056px) {
                    position: fixed;
                    top: 68px;
                    height: calc(100vh - 68px);
                    transform: translateX(-100%);
                    z-index: 1001;
                }

                .sidebar-close {
                    display: none;
                    position: absolute;
                    top: 16px;
                    right: -34px;
                    margin: 0 !important;
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                }

                &__box {
                    width: 100%;
                    height: 100%;
                    padding: 1.5rem 1.5rem;
                    overflow-y: scroll;

                    &::-webkit-scrollbar {
                        width: .4em;
                    }

                    &::-webkit-scrollbar,
                    &::-webkit-scrollbar-thumb {
                        overflow: visible;
                        border-radius: 4px;
                    }

                    &::-webkit-scrollbar-thumb {
                        background: rgba(0, 0, 0, .2);
                    }
                }

                .scroll-bar {
                    position: absolute;
                    background: #fff;
                    height: 100%;
                    top: 0;
                    right: 0;
                    width: 0.375rem;
                    -webkit-transition: all .5s;
                    opacity: 1;
                }

                &:hover .scroll-bar {
                    opacity: 0;
                    -webkit-transition: all .5s;
                }

                &.open {
                    transform: translateX(0);

                    .sidebar-close {
                        display: block;
                    }
                }
            }

            .layout-content {
                flex: 1;
            }
        }
    }
}